
#portfolio {
  padding: 20px 0 40px 0;

  .portfolio__nav {
    ul {
      display: flex;
      justify-content: space-between;
      li {
        padding: 5px 10px;
        margin-right: 10px;
        cursor: pointer;
        transition: 0.3s ease;
        font-size: 14px;
      }
      li:hover {
        background-color: $main-color;
      }
    }
    .portfolio__nav-active {
      background-color: $main-color;
    }
  }
  .portfolio__filter {
    display: flex;
    justify-content: center;
    cursor: pointer;
    margin-bottom: 50px;
    background-attachment: fixed;
    max-width: 200px;
    float: right;
    position: relative;
    padding: 8px 10px;
    

    p {
      padding-right: 10px;
    }

    .portfolio__filter-menu {
      background-color: $light-color;
      position: absolute;
      top: 40px;
      left: 0;
      width: 100%;
      height: 100px;
      text-align: center;
      border-radius: 5px;
      z-index: 999;
      p {
        padding: 8px 0;
        margin: 2px 0;
        transition: 0.3s ease;
      }
      p:hover {
        background-color: $main-color;
      }
    }
  }
}

.my-masonry-grid {
  display: -webkit-box; 
  display: -ms-flexbox; 
  display: flex;
  margin-left: -30px; 
  width: auto;
}
.mint__gallery {
  padding-left: 30px; 
  background-clip: padding-box;
}
 

.mint__gallery > div {
  margin-bottom: 30px;
}

